<template>
  <div class="container content">
    <div class="row">
      <div class="col-12">
        <h1 class="text-primary">Data Transaksi</h1>
      </div>
    </div>
    <div class="row mb-5">
      <div class="col-12">
        <div
          class="
            card
            mt-3
            rounded-10
            bg-putih
            shadow-sm
            border-primary
            text-primary
          "
        >
          <div class="card-header">Transaksi Hari Ini</div>
        </div>
      </div>
      <div v-if="this.transaksi.mutasi_hari_ini == ''" class="col-12 mt-2">
        <div class="alert alert-danger rounded-10" role="alert">
          Tidak ada transaksi!
        </div>
      </div>
      <div
        v-else-if="this.transaksi.mutasi_hari_ini != ''"
        v-for="mutasiHariIni in orderedTransaksiHariIni"
        :key="mutasiHariIni"
        class="col-12 mt-2"
      >
        <div
          v-if="mutasiHariIni.status == 'SUCCESS'"
          class="card rounded-10 bg-putih shadow-sm border-success text-primary"
        >
          <div class="card-body">
            <div class="text-success">
              <span class="float-left">{{
                format_date(mutasiHariIni.created_at)
              }}</span>
              <span class="float-right">{{ mutasiHariIni.status }}</span>
              <br />
              <div class="text-left">{{ mutasiHariIni.phone }}</div>
              <div class="text-left">({{ mutasiHariIni.code }})</div>
              <div class="text-left">{{ mutasiHariIni.note }}</div>
            </div>
            <router-link
              :to="{
                path: '/customer/bukti-transaksi/' + mutasiHariIni.trxid_api,
              }"
              class="btn btn-primary rounded-pill btn-block mt-3"
            >
              Lihat Bukti
            </router-link>
          </div>
        </div>
        <div
          v-if="mutasiHariIni.status == 'PENDING'"
          class="card rounded-10 bg-putih shadow-sm border-warning text-primary"
        >
          <div class="card-body">
            <div class="text-warning">
              <span class="float-left">{{
                format_date(mutasiHariIni.created_at)
              }}</span>
              <span class="float-right">{{ mutasiHariIni.status }}</span>
              <br />
              <div class="text-left">{{ mutasiHariIni.phone }}</div>
              <div class="text-left">({{ mutasiHariIni.code }})</div>
              <div class="text-left">{{ mutasiHariIni.note }}</div>
            </div>
            <button
              @click="updateTransaksi(mutasiHariIni.trxid_api)"
              class="btn btn-warning rounded-pill btn-block mt-3"
            >
              Refresh
            </button>
          </div>
        </div>
        <div
          v-if="mutasiHariIni.status == 'FAILED'"
          class="card rounded-10 bg-putih shadow-sm border-danger text-primary"
        >
          <div class="card-body">
            <div class="text-danger">
              <span class="float-left">{{
                format_date(mutasiHariIni.created_at)
              }}</span>
              <span class="float-right">{{ mutasiHariIni.status }}</span>
              <br />
              <div class="text-left">{{ mutasiHariIni.phone }}</div>
              <div class="text-left">({{ mutasiHariIni.code }})</div>
              <div class="text-left">{{ mutasiHariIni.note }}</div>
            </div>
            <router-link
              :to="{
                path: '/customer/bukti-transaksi/' + mutasiHariIni.trxid_api,
              }"
              class="btn btn-danger rounded-pill btn-block mt-3"
            >
              Lihat Bukti
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-12">
        <div
          class="
            card
            mt-3
            rounded-10
            bg-putih
            shadow-sm
            border-primary
            text-primary
          "
        >
          <div class="card-header">Riwayat Transaksi</div>
        </div>
      </div>
      <div
        v-show="this.transaksi.semua_mutasi_sebelumnya == ''"
        class="col-12 mt-2"
      >
        <div class="alert alert-danger rounded-10" role="alert">
          Tidak ada transaksi!
        </div>
      </div>
      <div
        v-show="this.transaksi.semua_mutasi_sebelumnya != ''"
        v-for="semua_mutasi_sebelumnya in orderedRiwayatTransaksi"
        :key="semua_mutasi_sebelumnya"
        class="col-12 mt-2"
      >
        <div
          v-show="semua_mutasi_sebelumnya.status == 'SUCCESS'"
          class="card rounded-10 bg-putih shadow-sm border-success text-primary"
        >
          <div class="card-body">
            <div class="text-success">
              <span class="float-left">{{
                format_date(semua_mutasi_sebelumnya.created_at)
              }}</span>
              <span class="float-right">{{
                semua_mutasi_sebelumnya.status
              }}</span>
              <br />
              <div class="text-left">{{ semua_mutasi_sebelumnya.phone }}</div>
              <div class="text-left">({{ semua_mutasi_sebelumnya.code }})</div>
              <div class="text-left">{{ semua_mutasi_sebelumnya.note }}</div>
            </div>
            <router-link
              :to="{
                path:
                  '/customer/bukti-transaksi/' +
                  semua_mutasi_sebelumnya.trxid_api,
              }"
              class="btn btn-primary rounded-pill btn-block mt-3"
            >
              Lihat Bukti
            </router-link>
          </div>
        </div>
        <div
          v-show="semua_mutasi_sebelumnya.status == 'PENDING'"
          class="card rounded-10 bg-putih shadow-sm border-warning text-primary"
        >
          <div class="card-body">
            <div class="text-warning">
              <span class="float-left">{{
                format_date(semua_mutasi_sebelumnya.created_at)
              }}</span>
              <span class="float-right">{{
                semua_mutasi_sebelumnya.status
              }}</span>
              <br />
              <div class="text-left">{{ semua_mutasi_sebelumnya.phone }}</div>
              <div class="text-left">({{ semua_mutasi_sebelumnya.code }})</div>
              <div class="text-left">{{ semua_mutasi_sebelumnya.note }}</div>
            </div>
            <button
              @click="updateTransaksi(semua_mutasi_sebelumnya.trxid_api)"
              class="btn btn-warning rounded-pill btn-block mt-3"
            >
              Refresh
            </button>
          </div>
        </div>
        <div
          v-show="semua_mutasi_sebelumnya.status == 'FAILED'"
          class="card rounded-10 bg-putih shadow-sm border-danger text-primary"
        >
          <div class="card-body">
            <div class="text-danger">
              <span class="float-left">{{
                format_date(semua_mutasi_sebelumnya.created_at)
              }}</span>
              <span class="float-right">{{
                semua_mutasi_sebelumnya.status
              }}</span>
              <br />
              <div class="text-left">{{ semua_mutasi_sebelumnya.phone }}</div>
              <div class="text-left">({{ semua_mutasi_sebelumnya.code }})</div>
              <div class="text-left">{{ semua_mutasi_sebelumnya.note }}</div>
            </div>
            <router-link
              :to="{
                path:
                  '/customer/bukti-transaksi/' +
                  semua_mutasi_sebelumnya.trxid_api,
              }"
              class="btn btn-danger rounded-pill btn-block mt-3"
            >
              Lihat Bukti
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Swal from "sweetalert2";
import axios from "axios";
import moment from "moment";
import _ from "lodash";

export default {
  name: "CusDataTransaksiView",
  props: ["ipAdress", "transaksi"],
  data() {
    return {
      user: JSON.parse(localStorage.getItem("SessionUser")),
      profileOnChange: null,
    };
  },
  methods: {
    updateTransaksi(value) {
      axios({
        method: "get",
        url:
          this.ipAdress +
          "api/transaksi/update/" +
          value +
          "?username=" +
          this.user.username,
      })
        .then((response) => {
          //handle success
          if (response.data.status == "failed") {
            Swal.fire({
              icon: "error",
              title: response.data.message,
              text: response.data.keterangan,
              showConfirmButton: false,
              timer: 1500,
            }).then(function () {
              window.location = "/customer/data-transaksi";
            });
          } else {
            Swal.fire({
              icon: "success",
              title: response.data.message,
              text: response.data.keterangan,
              showConfirmButton: false,
              timer: 1500,
            }).then(function () {
              window.location = "/customer/data-transaksi";
            });
          }
        })
        .catch((response) => {
          //handle error
          console.log(response);
        });
    },
    format_date(value) {
      if (value) {
        return moment(String(value)).format("DD-MMMM-YYYY HH:mm");
      }
    },
  },
  computed: {
    orderedRiwayatTransaksi: function () {
      return _.orderBy(this.transaksi.semua_mutasi_sebelumnya, "id", "desc");
    },
    orderedTransaksiHariIni: function () {
      return _.orderBy(this.transaksi.mutasi_hari_ini, "id", "desc");
    },
  },
};
</script>

<style>
html,
body {
  text-align: center;
}

body {
  background-color: #ffffff;
}

.rounded-2 {
  border-radius: 8px;
}

.rounded-5 {
  border-radius: 10px;
}

.rounded-10 {
  border-radius: 20px;
}

#navbottom {
  border-radius: 30px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.bg-biru {
  background-color: #1583dd;
}

.content {
  margin-top: 100px;
  margin-bottom: 130px;
}

.bg-putih {
  background-color: #ffffff;
}
</style>